<template>
  <div class="wrapper">
      <el-container>
		<img class="one" src="@/assets/image/3.jpg" alt="" style="width: 100%;height: 85vh;">
		<el-main>
			<Load v-if="!play1"></Load>
			<showPlay :play="play1" v-if="play1"></showPlay>
			
		</el-main>
		<el-footer>
			<div style="height: 20px;"></div>
			<footers1></footers1>
			<div style="height: 100px;"></div>
		</el-footer>
      </el-container>
  </div>
</template>

<script>
	import footers1 from "@/components/footers.vue"
	import showPlay from "@/components/showPlay.vue"
	import Load from "@/components/load.vue"
	export default {
		components: {
			footers1,
			showPlay,
			Load,
			
		},
		
		
		created() {
			
			this.queryPageSize()
		},
		
		
		data() {
			return {
				play1:"",
				obj1:"",
				type:3
			}
		},
		methods: {
			async queryPageSize(){
			   await this.$A.get('/api/querytArticleByType',{params: {type: this.type}})
				  .then(res =>{
					this.obj1 = res.data.data
				  })
			   this.getplay()
			},
			getExecStrs(str) {
			       var reg = /<img src="(.+?)\">/g
			           // var reg = /\$\{(.+?)\}/g
			       var list = []
			       var result = null
			       do {
			           result = reg.exec(str)
			           result && list.push(result[1])
			       } while (result)
							  if(list.length<=0){
								  return;
							  }
			       return list
			},
			getplay(){
			   let news = this.obj1.map((r,i) =>{
				   return{
					   id:r.id,
					   url:this.getExecStrs(r.content)==undefined?' ':this.getExecStrs(r.content)[0] ,
					   title:r.title,
					   content:r.content,
					   time:new Date(parseInt(r.time)).toLocaleString()
				   }
			   })
			   this.play1 = news
			}
		}
		
	}
</script>


<style>
	@media screen and (max-width:786px) {
		.one{
			height: 40vh !important;
		}
	}
</style>